<template>
  <div id="history">
    <Header>
      <el-breadcrumb separator-class="el-icon-arrow-right" slot="left">
        <el-breadcrumb-item>收入预算</el-breadcrumb-item>
        <el-breadcrumb-item>收入调整</el-breadcrumb-item>
      </el-breadcrumb>
      <div slot="right">
      </div>
    </Header>
    <div class="content">
      <el-row>
        <el-col :span="4">
          <el-date-picker v-model="yearValue" type="year" placeholder="选择年" @change="selectYearData"  format="yyyy 年" value-format="yyyy"></el-date-picker>
        </el-col>
        <el-col :span="8" :offset="1">
          <el-input placeholder="输入编号或预算体查询" v-model="keywords" @change="search" clearable>
            <el-button slot="append" icon="el-icon-search" @click="search"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <el-table :data="history" border style="width: 100%; margin-top: 20px">
        <el-table-column label="展开" type="expand" width="60px"> 
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">

              <el-row style="height:20px; background red">
                <el-col :span="24">
                    <el-form-item label="纵横轴:">
                      <span>
                        {{axisMap[props.row.axis]}}
                      </span>
                    </el-form-item>
                    <el-form-item label="定量定性:">
                      <span>
                         {{qqMap[props.row.qq]}}
                      </span>
                    </el-form-item>
                    <el-form-item label="正负项:">
                      <span>
                        {{pmMap[props.row.pm]}}
                      </span>
                    </el-form-item>
                    <el-form-item label="单位:">
                      <span>{{ props.row.unit}}</span>
                    </el-form-item>
                  </el-col>
              </el-row>

              <el-row style="height:20px; background red">
                <el-col :span="24">
                  <el-form-item label="操作后" style="width:8%; color: rgb(94, 94, 94);"></el-form-item>
                    <el-form-item label="2区间:">
                      <span>{{ props.row.after.line2 }}</span>
                    </el-form-item>
                    <el-form-item label="4区间:">
                      <span>{{ props.row.after.line4 }}</span>
                    </el-form-item>
                    <el-form-item label="6区间:">
                      <span>{{ props.row.after.line6 }}</span>
                    </el-form-item>
                    <el-form-item label="8区间:">
                      <span>{{ props.row.after.line8 }}</span>
                    </el-form-item>
                    <el-form-item label="10区间:">
                      <span>{{ props.row.after.line10 }}</span>
                    </el-form-item>
                  </el-col>
              </el-row>
              <el-row>
                <el-col :span="24">
                  <el-form-item label="操作前" style="width:8%;"></el-form-item>
                  <el-form-item label="2区间:">
                    <span>{{ props.row.before.line2 }}</span>
                  </el-form-item>
                  <el-form-item label="4区间:">
                    <span>{{ props.row.before.line4 }}</span>
                  </el-form-item>
                  <el-form-item label="6区间:">
                    <span>{{ props.row.before.line6 }}</span>
                  </el-form-item>
                  <el-form-item label="8区间:">
                    <span>{{ props.row.before.line8 }}</span>
                  </el-form-item>
                  <el-form-item label="10区间:">
                    <span>{{ props.row.before.line10 }}</span>
                  </el-form-item>
                </el-col>
              </el-row>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column prop="userName" label="姓名"></el-table-column>
        <el-table-column prop="jobnumber" label="工号" ></el-table-column>
        <el-table-column prop="period" label="周期"></el-table-column>
        <el-table-column label="类型">
          <template slot-scope="scope">
            {{scope.row.code}} / {{scope.row.typeName}}
          </template>
        </el-table-column>
        <el-table-column prop="weights" label="权重"></el-table-column>
       
				<el-table-column label="调整时间">
          <template slot-scope="scope">
            {{parseTime(scope.row.createTime)}}
          </template>
        </el-table-column>
        <el-table-column prop="manager.userName" label="操作人"></el-table-column>
        <el-table-column label="数据来源" width="150">
          <template slot-scope="scope">
            {{changeTypeMap[scope.row.changeType]}}
          </template>
        </el-table-column>
      </el-table>

      <el-pagination :current-page="page" :page-size="15" layout="total, prev, pager, next, jumper" :total="count" align="right" @current-change="changePage" class="pagi">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import Header from '../../components/Header'
  export default {
    name: 'history',
    components: {
      Header
    },
    data() {
      return {
        changeTypeMap: {
          0: '导入',
          1: '增加',
          2: '修改',
          3: '删除',
        },
        typeMap: {
          0: '系统定义',
          1: '用户自建'
        },
        qqMap: {
          1: '定量',
          2: '定性'
        },
        axisMap: {
          1: '横轴',
          2: '纵轴'
        },
        pmMap: {
          1: '正',
          2: '负'
        },
        yes1: false,
        yes2: false,
        titleName: "",
        typename: [],
        typeData: {
          year: new Date().getFullYear()
        },
        rules: {
          userName: [
            { required: true, message: '请输入姓名', trigger: 'blur' },
            { min: 2, message: '最少输入2个字符', trigger: 'blur' }
          ],
          jobnumber: [{ required: true, message: '请输入工号', trigger: 'blur'},
            { min: 2, message: '最少输入3个字符', trigger: 'blur'}
          ],
          period: [{ required: true, message: '请输入周期', trigger: 'blur'},
            { min: 2, message: '最少输入3个字符', trigger: 'blur'}
          ],
          incomings: [{ required: true, message: '请输入目标', trigger: 'blur'},
            { min: 2, message: '最少输入3个字符', trigger: 'blur'}
          ],
          line2: [{ required: true, message: '请输入数值', trigger: 'blur'},
            { min: 2, message: '最少输入3个字符', trigger: 'blur'}
          ],
          line4: [{ required: true, message: '请输入数值', trigger: 'blur'},
            { min: 2, message: '最少输入3个字符', trigger: 'blur'}
          ],
          line6: [{ required: true, message: '请输入数值', trigger: 'blur'},
            { min: 2, message: '最少输入3个字符', trigger: 'blur'}
          ],
          line8: [{ required: true, message: '请输入数值', trigger: 'blur'},
            { min: 2, message: '最少输入3个字符', trigger: 'blur'}
          ],
          line10: [{ required: true, message: '请输入数值', trigger: 'blur'},
            { min: 2, message: '最少输入3个字符', trigger: 'blur'}
          ],
          weights: [{ required: true, message: '请输入数值', trigger: 'blur'},
            { min: 2, message: '最少输入3个字符', trigger: 'blur'}
          ],
          code: [
            { required: true, message: '请选择活动区域', trigger: 'change' }
          ],
        },
        dialogShow:false,
        history: [],
        page: 1,
        limit: 10,
        count: 0,
        year: new Date().getFullYear(),
        keywords: '',
        yearValue: new Date()
      }
    },
    methods: {
      parseTime(date) {
				date = new Date(date)
				return `${date.getFullYear()}-${date.getMonth() +1}-${date.getDate()} ${date.getHours()}:${date.getMinutes()}`
			},
      search() {
        this.page = 1;
        this.keywords = this.keywords.trim();
        this.getHistory()
      },
      selectYearData() {
        this.page = 1;
        this.year = new Date(this.yearValue).getFullYear();
        this.getHistory()
      },
      getHistory() {
        this.$http.get(`/ec/api/incomings/history?year=${this.year}&page=${this.page}&limit=${this.limit}&keywords=${this.keywords}`).then(res => {
          let resData = res.data;
          if (resData.errcode === 0) {
            this.count = (resData.data.count || 0);
            this.history = resData.data.rows || []
          }
        })
      },
      changePage(page) {
        this.page = page;
        this.getHistory()
      }
    },
    created() {
      this.page = this.$route.query.page || 1;
      this.getHistory()
    }
  }
</script>

<style>
.pagi {
  margin: 10px;
}
.el-form--inline .el-form-item__label {
  color: rgb(131, 130, 130);
  font-weight: bold;
}
.el-select>.el-input {
  width: 202px;
}
